.wrapper {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.message {
  flex: 0 0 75%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 50%;
  background-color: #1b1d31;
  font-family: "Prompt", sans-serif;
  color: #fff;
  font-size: 2rem;
}
